const log = console.log.bind(console)

window.$ = window.jQuery = function (selectorOrArray) {
    let elements
    if(typeof selectorOrArray === 'string'){
        elements = document.querySelectorAll(selectorOrArray)
    } else if(selectorOrArray instanceof Array){
        elements = selectorOrArray
    }
    const api = Object.create(jQuery.prototype)
    Object.assign(api, {
        elements: elements,
        oldApi: selectorOrArray
    })
    return api
}

jQuery.fn = jQuery.prototype = {
    jquery: true,
    // constructor: jQuery,
    addClass(className){
        for(let i=0;i<elements.length;i++){
            elements[i].classList.add(className)
            // ç”¨é—­åŒ…æ¥ç»´æŒå‡½æ•°ï¼ˆelements - addClassï¼‰
        }
        return this // return çš„å¯¹è±¡å°±æ˜¯è°ƒç”¨å‡½æ•°é‡Œé¢çš„ this
    },
    find(selector){
        let array = []
        for (let i = 0; i < elements.length; i++) {
            const elements2 = Array.from(elements[i].querySelectorAll(selector))
            // ä¸å˜ä¸‹é¢è¿žæŽ¥ç»“æžœå°±ä¸æ˜¯çœŸæ­£çš„æ•°ç»„äº†
            array = array.concat(elements2)
        }
        array.oldApi = this // this æ˜¯æ—§çš„ api
        return jQuery(array)
    },
    end(){
        return this.oldApi // this æ˜¯æ–°çš„ api
    },
    each(fn){
        for (let i = 0; i < elements.length; i++){
            fn.call(null, elements[i], i)
        }
        return this
    },
    parent(){
        const array = []
        this.each((node)=>{
            if(array.indexOf(node.parentNode) === -1){
                array.push(node.parentNode)
            }
        })
        return jQuery(array)
    },
    children(){
        const array = []
        this.each((node)=>{
            array.push(...node.children)
        })
        return jQuery(array)
    },
    print(){
        log(elements)
    }
}